.weather-container {
  display: flex;
  flex-direction: row;
  background-color: #f6f8fb;
  border-radius: 15px;
  overflow: hidden;
  width: 100%;
  max-width: 1200px;
  margin: 20px auto;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.1);
}

.sidebar {
  width: 30%;
  background-color: white;
  padding: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  border-right: 1px solid #ddd;
}

.search-form {
  width: 100%;
  margin-bottom: 20px;
}

.search-input {
  width: 90%;
  padding: 10px;
  border: 1px solid #ddd;
  border-radius: 25px;
  outline: none;
  font-size: 16px;
  text-align: center;
}

.current-weather {
  text-align: center;
}

.temperature {
  font-size: 48px;
  font-weight: bold;
  margin: 10px 0;
}

.date-time,
.weather-description,
.location {
  font-size: 18px;
  margin: 5px 0;
}

.main-content {
  width: 70%;
  padding: 20px;
}

.forecast-tabs {
  display: flex;
  justify-content: space-between;
  margin-bottom: 20px;
}

.tab {
  padding: 10px 20px;
  border: none;
  background-color: #f6f8fb;
  border-radius: 25px;
  cursor: pointer;
}

.tab.active {
  background-color: #fff;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

.forecast {
  display: flex;
  justify-content: space-between;
  margin-bottom: 20px;
}

.forecast-day {
  background-color: #fff;
  border-radius: 10px;
  padding: 10px;
  text-align: center;
  width: 18%;
}

.forecast-day-date,
.forecast-day-temp {
  font-size: 16px;
  margin: 5px 0;
}

.highlights {
  display: flex;
  flex-wrap: wrap;
  max-width: 20%;
  justify-content: space-between;
}

.highlight {
  background-color: #fff;
  border-radius: 10px;
  padding: 20px;
  width: 48%;
  margin-bottom: 20px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

.highlight h3 {
  margin-bottom: 10px;
  font-size: 16px;
  color: #555;
}

.highlight p {
  font-size: 14px;
  color: #333;
}
